import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import 'bootstrap/dist/css/bootstrap.min.css';

const listOfData = [
    [1, 'C', 'My C Code'],
    [2, 'C++', 'My C++ Code'],
    [3, 'Java', 'My Java Code']
];

ReactDOM.render(<App listOfData={listOfData}/>, document.getElementById('root'));

